* {
    margin: 0;
    padding: 0;
    text-decoration: none;
}

header {
    color: white;
    position: fixed;

    width: 250px;
    height: 100vh;

    padding: 0 20px;

    background-color: #282c34;
    border-right: 1px solid rgb(66, 66, 66);
    box-sizing: border-box;
}

h1 {
    display: flex;
    align-items: center;

    height: 80px;
    border-bottom: 1px solid rgb(66, 66, 66);
}

nav {
    color: white;
    display: flex;
    flex-direction: column;
    gap: 10px;

    border-bottom: 1px solid rgb(66, 66, 66);
}

.logInBtn {
    text-align: start;
    width: 100%;
    height: 50px;

    cursor: pointer;

    font-size: 25px;
    font-weight: 700;

    border: none;
    border-bottom: 1px solid rgb(66, 66, 66);

    color: rgb(66, 66, 66);
    background-color: #282c34;
}

.btnArea {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 20px 0;
    gap: 20px;
}

.bottomBtn {
    width: 80%;
    height: 50px;

    border: none;
    background-color: #282c34;
    cursor: pointer;

    border: 1px solid rgb(66, 66, 66);
    border-radius: 3%;
}

nav a {
    color: white;
    display: flex;
    height: 50px;

    align-items: center;

    font-size: 25px;
    font-weight: 700;
    opacity: .5;
}

nav a:hover {
    opacity: 1;
}